import { addExternalFn, addExternalFnDirect } from "external/external";
import { makeDiagramFromEdit } from "diagram/edit/convert/from";
import React from "react";
import { EditDiagram } from "../../edit/edit";
import { DiagramAction } from "../reducer/reducer";
import { ParseJsonPayload } from "../reducer/parse/json";

const registerImport = addExternalFn("importDiagram");

export const useDiagramExternal = (
    diagram: EditDiagram,
    diagramDispatch: React.Dispatch<DiagramAction>
) => {
    React.useEffect(() => {
        registerImport((text: string) => {
            const payload: ParseJsonPayload = { text, isAppend: false };
            diagramDispatch({ type: "parse-json", payload });
        });
    }, [diagramDispatch]);
    React.useEffect(() => {
        addExternalFnDirect("exportDiagram", () => {
            return JSON.stringify(makeDiagramFromEdit(diagram));
        });
    }, [diagram]);
};
