import { background } from "@moai/core";
import { Edge } from "app/edge/edge";
import { Table } from "app/table/table";
import { EditDiagram, EditEdge, EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { debugLog } from "utils/debug";
import { SelectDragLayer } from "../select/canvas/drag/drag";
import { SetSelectIds } from "../select/select";
import s from "./canvas.module.scss";

interface Props {
    // Diagram
    diagram: EditDiagram;
    diagramDispatch: React.Dispatch<DiagramAction>;
    // View
    isRowsExpand: boolean;
    zoom: number;
    viewportRef: React.RefObject<HTMLDivElement>;
    forceVisible: boolean;
    // Select
    selectedTables: Set<number>;
    selectedRefs: Set<number>;
    setSelectIds: SetSelectIds;
}

const getContainerStyle = (props: Props): React.CSSProperties => {
    const [{ zoom }, { width, height }] = [props, props.diagram.size];
    const halfMargin = (zoom - 1) / 2;
    const margin = `${height * halfMargin}px ${width * halfMargin}px`;
    const transform = zoom !== 1 ? `scale(${zoom})` : "";
    return { width, height, transform, margin, fontSize: 12 };
};

type TablesMap = Map<number, EditTable>;

const toTablesMap = (map: TablesMap, table: EditTable): TablesMap => {
    map.set(table.id, table);
    return map;
};

export interface RelatedEdge {
    edge: EditEdge;
    table: EditTable;
}
type RelatedEdgesMap = Map<number, RelatedEdge[]>;

const toRelatedEdgesMap = (tablesMap: TablesMap) => {
    return (map: RelatedEdgesMap, edge: EditEdge) => {
        const toTable = tablesMap.get(edge.to.table) as EditTable;
        const fromTable = tablesMap.get(edge.from.table) as EditTable;
        // From table
        const fromKey = edge.from.table;
        const fromEdges = map.get(fromKey) ?? [];
        map.set(fromKey, fromEdges.concat({ edge, table: toTable }));
        // To table
        const toKey = edge.to.table;
        const toEdges = map.get(toKey) ?? [];
        map.set(toKey, toEdges.concat({ edge, table: fromTable }));
        return map;
    };
};

// To have stable identity
const emptyArray: RelatedEdge[] = [];

export const Canvas = (props: Props) => {
    const { refs, tables } = props.diagram;
    debugLog("Canvas");

    const tablesMap = React.useMemo(() => {
        return tables.reduce(toTablesMap, new Map());
    }, [tables]);

    const edgesMap = React.useMemo(() => {
        return refs.reduce(toRelatedEdgesMap(tablesMap), new Map());
    }, [refs, tablesMap]);

    return (
        <div
            id="canvas"
            className={[s.container, background.secondary].join(" ")}
            style={getContainerStyle(props)}
        >
            {/* Separate grid so both grid and background color is included
            in exported image */}
            <div className={s.grid} />
            <SelectDragLayer
                diagram={props.diagram}
                setSelectIds={props.setSelectIds}
            />
            <svg
                className={s.edges}
                width={props.diagram.size.width}
                height={props.diagram.size.height}
            >
                {props.diagram.refs.map((edge) => (
                    <Edge
                        key={edge.id}
                        edge={edge}
                        // Select
                        isSelected={props.selectedRefs.has(edge.id)}
                        setSelectIds={props.setSelectIds}
                        // Tables
                        fromTable={tablesMap.get(edge.from.table) as EditTable}
                        toTable={tablesMap.get(edge.to.table) as EditTable}
                        // Diagram
                        diagramDispatch={props.diagramDispatch}
                    />
                ))}
            </svg>
            {props.diagram.tables.map((table) => (
                <Table
                    key={table.id}
                    // Table
                    table={table}
                    // Select
                    isSelected={props.selectedTables.has(table.id)}
                    setSelectId={props.setSelectIds}
                    // Diagram
                    isRowsExpand={props.isRowsExpand}
                    columns={props.diagram.columns}
                    relatedEdges={edgesMap.get(table.id) ?? emptyArray}
                    diagramDispatch={props.diagramDispatch}
                    // View
                    viewportRef={props.viewportRef}
                    zoom={props.zoom}
                    forceVisible={props.forceVisible}
                />
            ))}
        </div>
    );
};
