import { ButtonMenu } from "@moai/core";
import { ParseJsonPayload } from "diagram/state/reducer/parse/json";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";
import json1 from "./1.json";
import json2 from "./2.json";
import json3 from "./3.json";

interface Props {
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const parse = (props: Props, json: object) => {
    const text = JSON.stringify(json);
    const payload: ParseJsonPayload = { text, isAppend: false };
    props.diagramDispatch({ type: "parse-json", payload });
};

export const HeaderSample = (props: Props) => (
    <ButtonMenu
        actions={[
            { label: "3 tables (1KB)", fn: () => parse(props, json1) },
            { label: "3 tables (9KB)", fn: () => parse(props, json2) },
            { label: "28 tables (28KB)", fn: () => parse(props, json3) },
        ]}
    />
);
